.App {
  text-align: center;
}

.Categories, .Orders {
  align-items: center;
  justify-content: center;
  background-color: #056644;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  min-height: 10vh;
  padding: 1vh;
}

.Content {
  background-color: #05AC72;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Exam {
  padding-top: 2%;
}


.Species {
  margin-top: 1%;
  max-width: 50vw;
  height: auto;
}

.StartExam {
  margin-right: 1%;
}

.Category {
  margin-right: 1%;
}

.Order {
  margin-right: 1%;
}


.Active, .StartedExam {
  background-color: green;
  font-weight: 700;
  margin-right: 1%;
  font-size: calc(10px + 3vmin);
}

.Specimen {

}

.Specimen p.description {
  margin: 5% 20% 0 20%;
  text-align: left;
}

.Thumbnails {
   display: flex;
   flex-direction: row;
   flex-wrap: nowrap;
   margin-left: auto;
   margin-right: auto;
   justify-content: center;
}

.Thumbnail {
  margin-right: 1vw;
  width: 8vw;
  height: 8vw;
}

.Pictures {
  position: relative;
}

.Arrows {
  background-color: #05AC72;
  display: flex;
  font-size: calc(10px + 2vmin);
  flex-direction: row;
  justify-content: space-around;
}

.Next {
  order: 1;
}

.Previous {
  order: 0;
}

.NameHelp {
  margin-left: auto;
  margin-right: auto;
  background-color: #f5d49d;
  border: 1px dashed black;
  color: black;
  width: 50vw;
}

.Tip {
  margin-left: auto;
  margin-right: auto;
  background-color: #f5d49d;
  border: 1px dashed black;
  color: black;
  width: 50vw;
}

.SkeletonParagraph {
  border: 1px dashed black;
  color: black;
  left: 5%;
  top: 0;
  position: absolute;
}

.StartButton {
  border: 1px dashed black;
  color: black;
}

.Timer {
  border: 1px dashed black;
  color: black;
  padding: 1%;
  position: absolute;
  margin-left: 80vw;
}
